@import "../../global.scss";
.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    h1{
        font-size: 80px;
        flex-direction: column;
        overflow: hidden;

        @include mobile{
            font-size: 20px;
        }

    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        overflow: hidden;
       
        @include mobile{
            padding-left: 25px;
        flex-wrap: wrap;
        margin: 10px 0;
        justify-content: center;
      
};
      
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        overflow: hidden;

        @include mobile{
        width: 100%;
        }
        .item{
            width: 220px;
            height: 190px;
            border-radius: 20px;
            border: 1px solid white;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            color: white;
            @include mobile{
                width: 120px;
                height: 140px;
            }
            &::-webkit-scrollbar{
                display: none;
                position: relative;
                transition: all .5s ease;
                cursor: pointer;
                   

                h3{
                    position: absolute;                    
                    font-size: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                }


                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    z-index: 1;

                }
            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}